.App {
  
}

.map{
  width: 100vw;
  height: 100vh;
}
.btn{
  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 999;
  background-color: cornflowerblue;
  color: white;
  border: none;
  padding: 20px;
  border-radius: 60px;
  outline: none;
  cursor: pointer;
  box-shadow: 0px 2px 10px black;
  transition: .3s;
}
.btn:hover{
  box-shadow: 0px 0px 5px black;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
